/* mobile */

.slider__control {
  display: none;
}

.slider {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
  width: 200px;
  margin: 0 auto;
}

.slider__circle {
  width: 8px;
  height: 8px;

  background-color: #ffffff;
  border-radius: 50%;
  opacity: 0.3;

  &:hover,
  &:active {
    width: 10px;
    height: 10px;

    opacity: 1;
  }
}

/* tablet */

@media (min-width: @tablet-width) {
  .circle-4,
  .circle-5,
  .circle-6,
  .circle-7 {
    display: none;
  }

  .slider {
    align-items: center;
  }

  .slider__control {
    position: relative;

    display: block;
    width: 48px;
    height: 48px;

    border-radius: 50%;
    border: 2px solid rgba(255, 255, 255, 0.1);

    &:hover {
      .slider__arrow {
        fill: #f48e06;
      }
    }
  }

  .slider__arrow {
    position: absolute;
    top: 10px;

    width: 15px;
    height: 23px;

    fill: #ffffff;
  }

  .slider__arrow--left {
    left: 12px;
  }

  .slider__arrow--right {
    right: 14px;
  }
}

