/* mobile */

.page-main__wrapper {
  min-width: 375px;
  margin: 0 auto;
  padding-top: 70px;
  padding-right: 15px;
  padding-bottom: 135px;
  padding-left: 15px;
}

.page-main__title {
  margin: 0;
  margin-bottom: 20px;

  font-size: 32px;
  line-height: 31px;
  text-align: center;
}

/* tablet */

@media (min-width: @tablet-width) {
  .page-main__wrapper {
    min-width: 768px;
    padding-right: 65px;
    padding-bottom: 165px;
    padding-left: 65px;
  }
}

/* desktop */

@media (min-width: @desktop-width) {
  .page-main__wrapper {
    width: 1280px;
    padding-bottom: 130px;
  }

  .page-main__title {
    text-align: left;
  }
}
