/* mobile */

.item-lol,
.item-player,
.item-gta,
.item-overwatch,
.item-apex {
  display: none;
}

.games {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
  padding-bottom: 50px;
}

.games__link {
  position: relative;

  display: block;

  font-size: 0;
  line-height: 0;
}

.games__title-wrapper {
  position: absolute;
  bottom: 0;
  left: 0;

  width: 100%;
  height: 118px;
  padding-top: 24px;
  padding-left: 24px;

  background: linear-gradient(180deg, rgba(0, 0, 0, 0.85) 0%, rgba(0, 0, 0, 0) 100%);
  transform: matrix(1, 0, 0, -1, 0, 0);
}

.games__title {
  margin: 0;

  font-size: 24px;
  line-height: 130%;
  color: #ffffff;

  transform: matrix(1, 0, 0, -1, 0, 0);
}

/* tablet */

@media (min-width: @tablet-width) {
  .item-lol,
  .item-player,
  .item-gta {
    display: block;
  }

  .games {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 640px;
  }

  .games__item {
    margin-bottom: 40px;
  }

  .title-player {
    text-transform: uppercase;
  }
}

/* desktop */

@media (min-width: @desktop-width) {
  .item-overwatch,
  .item-apex {
    display: block;
  }

  .games {
    width: 1151px;
  }
}
